html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
}

img {
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

[class$=__container] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  margin: 0;
  line-height: 1.3;
}

h2 {
  margin: 0;
  line-height: 1.3;
}

h3 {
  margin: 0;
  line-height: 1.3;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=__heading] {
  margin: 2rem 0;
}

a {
  font-size: 1.8rem;
  text-decoration: none;
  color: #080708;
}

p {
  font-size: 2rem;
  line-height: 1.5;
  margin: 0;
}

/* Variables */
.header {
  padding: 2rem;
  background-color: #9880dd;
}
.header__container {
  display: flex;
  justify-content: space-between;
  color: #ffffff;
}
.header__link {
  text-decoration: none;
  color: #ffffff;
}
.header__link-title {
  font-weight: 700;
}

.main {
  padding: 3rem 0;
  min-height: 75vh;
  background-color: #e5e5e5;
}
@media (min-width: 768px) {
  .main {
    padding: 7rem 0;
  }
}

.searcher {
  padding-bottom: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2.5rem;
  text-align: center;
}
.searcher__title {
  font-weight: 700;
  color: #F7AEF8;
  max-width: 60rem;
}

.results {
  padding-bottom: 4rem;
}

.footer {
  padding: 2rem 0;
  background-color: #F7AEF8;
  text-align: center;
  color: #080708;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .nav {
    width: auto;
    flex-direction: row;
    align-items: end;
    gap: 1.5rem;
  }
}
.nav__link {
  padding: 1rem;
  width: auto;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  text-decoration: none;
  color: #ffffff;
  border-bottom: 0.3rem solid transparent;
}
@media (min-width: 768px) {
  .nav__link {
    padding: 0.3rem;
  }
}
@media (hover: hover) {
  .nav__link {
    transition: border 0.3s ease-in-out;
  }
  .nav__link:hover {
    border-bottom: 0.3rem solid #ffffff;
  }
}

.searcher-form {
  width: 100%;
  max-width: 60rem;
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}
.searcher-form__input {
  border: 0.2rem solid rgb(217.2546583851, 208.4596273292, 242.5403726708);
  border-radius: 2rem;
  outline: none;
  padding: 1.3rem;
  flex: 1;
  color: #9880dd;
  transition: border 0.1s ease-in-out;
}
.searcher-form__input:focus {
  border-color: #9880dd;
}
.searcher-form__btn {
  background-color: #9880dd;
  color: #fff;
  border: none;
  border-radius: 2rem;
  padding: 1.3rem 3rem;
  font-weight: 700;
  text-transform: uppercase;
}
@media (hover: hover) {
  .searcher-form__btn {
    transition: background-color 0.3s ease-in-out;
  }
  .searcher-form__btn:hover {
    background-color: #F7AEF8;
    cursor: pointer;
  }
}

.results-gallery__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .results-gallery__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .results-gallery__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}

.gallery-item {
  position: relative;
  border-radius: 1rem;
  background-color: #ffffff;
  overflow: hidden;
}
.gallery-item::after {
  content: "";
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  z-index: 0;
}
@media (hover: hover) {
  .gallery-item {
    transition: all 0.3s ease-in-out;
  }
  .gallery-item:hover {
    cursor: pointer;
  }
  .gallery-item:hover::after {
    opacity: 1;
  }
  .gallery-item:hover .gallery-item__info {
    opacity: 1;
    transform: translateY(0);
    z-index: 1;
  }
}
.gallery-item__img {
  width: 100%;
  border-radius: inherit;
  object-fit: cover;
  aspect-ratio: 5/4;
}
.gallery-item__info {
  position: absolute;
  inset: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  color: #ffffff;
  opacity: 0;
  transform: translateY(30%);
  transition: opacity 0.3s ease, transform 0.3s ease;
}
.gallery-item__likes {
  font-weight: 500;
  margin: 0;
}
.gallery-item__btn {
  margin-top: 2rem;
  display: block;
  padding: 1rem 2rem;
  background-color: #9880dd;
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .gallery-item__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .gallery-item__btn {
    transition: background-color 0.3s ease-in-out;
  }
  .gallery-item__btn:hover {
    background-color: rgb(129.6726708075, 100.4701863354, 213.6298136646);
    cursor: pointer;
  }
}
.gallery-item__btn {
  margin-top: 1rem;
}

.alert {
  margin: 0.5rem 0 0;
  border: 0.1rem solid transparent;
  border-radius: 1rem;
  padding: 1rem 2rem;
  text-align: center;
  line-height: normal;
}
.alert--error {
  border-color: #ac0000;
  background-color: #fbe9e7;
  color: #ac0000;
}
.alert--success {
  border-color: #049632;
  background-color: #d3f9bb;
  color: #049632;
}

.pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 2rem;
}
.pagination__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 3rem;
  padding: 1.5rem;
  border-radius: 50%;
  border: none;
  background-color: #9880dd;
  color: #ffffff;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
}
@media (hover: hover) {
  .pagination__btn {
    transition: background-color 0.3s ease-in-out;
  }
  .pagination__btn:hover {
    background-color: #F7AEF8;
  }
}